body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  font-size: 14px;
  line-height: 1.4;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.te-collection .te-product-card {
  width: auto;
}

.te-logo {
  height: 30px;
  pointer-events: none;
}

.header {
  background-color:#f3f3f3;
  height: 50px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  position: fixed;
  width: 100vw;
  z-index: 10;
  top: 0;
}

.menu {
  list-style: none;
  padding-left: 0;
}

.menu-item {
  padding-left: 35px;
  text-transform: uppercase;
  color: white;
}

.navigation {
  display: block;
  padding: 5px 20px;
}

.horizontal-menu {
  list-style: none;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-flex-wrap: nowrap;
          flex-wrap: nowrap;
  -webkit-align-items: center;
          align-items: center;
  margin: 0;
  padding: 0;
}

.horizontal-item {
  padding: 0;
  margin: 0 20px 0 0;
  color: #fefefe;
  text-decoration: none;
  font-weight: 500;
  font-size: 1.2em;
  cursor: pointer;
  -webkit-transition: color .4s ease;
  transition: color .4s ease;
}

.te-cart {
  margin-left: auto;  
}
.cart-icon .tipser-icon-cart {
  color: black;
  font-size: 20px;
}

.link {
  color: blue;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

footer {
  height: 300px;
  background: #eee;
  text-align: center;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  border-top: 1px solid #e0e0e0;
}

.footer-text {
  color: #bbbbbb;
  font-size: calc(10px + 2vmin);
}

